import React, { useState } from 'react';
import './App.css';
import './index.css'
import TodoList from './TodoList'

function App() {
  const [listItem, setList]=useState()
  const [items, setItems] = useState([])

  const updateList = ()=>{
      setItems( (oldItems =>{
        return [...oldItems, listItem]
      }))
      setList('')
  }
  const getValue=(event)=>{
      let getTheValue = event.target.value;
       setList(getTheValue)
  }

  const deleteItems = (id)=>{
   setItems ((oldItems)=>{
     return oldItems.filter((arrayElement, index)=>{
       return index!==id;
     })
   })
      
  }
  return (
    <div className="App">
      <div className='app-container'>
        <h1>Todo List</h1>
        <input type='text' placeholder='Enter Your Item' value={listItem} onChange={getValue} /> 
        <button className='fas fa-plus' onClick={updateList}></button>
        <ol>
           {items.map( (value, index) => {
              return (
                <TodoList key={index} id={index} text ={value} onSelect = {deleteItems} />
              )
           })}
        </ol>
      </div>
    </div>
  );
}

export default App;
